@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@100;200;300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@100;200;300;400;500;600;800;900&display=swap);



* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-decoration: none;
  list-style: none;
}




:root {
  --Comforta: "Comfortaa";
  --Nunito: "Nunito";
  --Sacramento: "Sacramento";
  --Poppins: "Poppins";
  --Dark-Orange: #ff8c00;
  --White: #fff;
  --Dark-Blue: #263361;
  --Blue-Violet: blueviolet;
  --Color-1: #8ADFE3;
  --Color-2: #55C5D1;
  --Color-3: #4699C2;
  --Color-4: #FFD57E;
  --Color-5: #F79C65;
  --Color-6: #FC8476;
  --Color-7: #E25B45;
  --Color-8: #FF8357;
  --Color-9: #E7E7E7;
  --Color-10: #F5CEC7;
  --Color-11: #5A6794;
  --Color-12: #565A97;
  --Color-13: #ADC865;
  --Color-14: #b6b6b6;
}

body {
  margin: 0;
  font-family: 'Comfortaa', display;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body::-webkit-scrollbar {
  width: 5px;               /* width of the entire scrollbar */
}

body::-webkit-scrollbar-track {
  background: var(--Color-14);        /* color of the tracking area */
}

body::-webkit-scrollbar-thumb {
  background-color: var(--Color-11);    /* color of the scroll thumb */
  border-radius: 20px;       /* roundness of the scroll thumb */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
